import React , {useState}from 'react'
import "../snaks/snaks"
//import { aqua } from '../../../data/database'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from "react-router-dom";
import egi from "../../../png/foot/egi.png"
import usrici from "../../../png/foot/.png"
import krevetki from "../../../png/foot/krevetki.png"
import grebesh from "../../../png/foot/grebeshek.png"
import Shop from '../../burger//Shop';
import midi from "../../../png/foot/midii2.png"

const Aqua = () => {

  const ru = useSelector(state=>state.one)

  const dispath = useDispatch()

  const list = useSelector(state=>state.chek)
  const list2 = useSelector(state=>state.chek_en)

  const [count,setcount] = useState(list.reduce(function(sum,num){return num.count + sum},0))

  function func(e){
    const target = e.target.textContent
    
    if (ru==="ru"){
      dispath({type:"add",payload:target})
      setcount(list.reduce(function(sum,num){return num.count + sum},0))
    }

    if (ru==="en"){
      dispath({type:"add_en",payload:target})
      setcount(list2.reduce(function(sum,num){return num.count + sum},0))
    }
    
    

    
    //console.log(" = " + count)
  }
  

  return (
    <>
    <Shop count={count}/>
     <div className="skakes:">
     <Link to="/menu" className='btn-back' ></Link>
    <div className='snakes-item'>{ru==="ru"?"":"Aquarium"}</div>

    <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?"":"Mussels"}</div>
            <div className="wr_img"><img src={midi} alt="img" /></div>
            <span className='price-pl'>500 / 870 </span>
          </div>


    <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Sea urchin"}</div>
            <div className="wr_img"><img src={egi} alt="img" /></div>
            <span className='price-pl'>1 / 670 </span>
          </div>
          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Khasan oyster"}</div>
            {/* <div className="wr_img"><img src={egi} alt="" /></div> */}
            <span className='price-pl'>1 / 320 </span>
          </div>

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Romarin oyster"}</div>
            {/* <div className="wr_img"><img src={egi} alt="" /></div> */}
            <span className='price-pl'>1 / 320 </span>
          </div>

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Imperial oyster"}</div>
            {/* <div className="wr_img"><img src={egi} alt="" /></div> */}
            <span className='price-pl'>1 / 320 </span>
          </div>

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Oyster Casablanca"}</div>
            {/* <div className="wr_img"><img src={egi} alt="" /></div> */}
            <span className='price-pl'>1 / 420 </span>
          </div>

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Royal oyster"}</div>
            {/* <div className="wr_img"><img src={egi} alt="img" /></div> */}
            <span className='price-pl'>1 / 420 </span>
          </div> 

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?"  ":"Oyster Pink Jolie"}</div>
            <div className="wr_img"><img src={usrici} alt="img" /></div>
            <span className='price-pl'>1 / 420 </span>
          </div> 

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Magadan shrimps"}</div>
            <div className="wr_img"><img src={krevetki} alt="img" /></div>
            <span className='price-pl'>1590 </span>
          </div> 

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?" ":"Sakhalin scallop"}</div>
            <div className="wr_img"><img src={grebesh} alt="img" /></div>
            <span className='price-pl'>1 / 590 </span>
          </div> 

          <div className='item_pal' tabIndex="0" >
            <div className='label' onClick={func}>{ru==="ru"?"":"Spizula"}</div>
            {/* <div className="wr_img"><img src={grebesh} alt="" /></div> */}
            <span className='price-pl'>1 / 490 </span>
          </div> 
    
    
    {/* {aqua.map(el=>(
        <div className='item' tabIndex="0" onClick={func}><span className='label'>{ru==="ru"?el.name:el.name_en}</span><span className='price'>{el.price} </span></div>
    ))} */}


    </div>
    </>
  )
}

export default Aqua